import PropTypes from 'prop-types';
import { Modal, ModalBody, FormGroup, Label } from 'reactstrap';
import { useEffect, useState } from 'react';
import * as pdfMake from 'pdfmake/build/pdfmake';
import useAxios from '../../hooks/useAxios';
import { alert } from '../../components/atoms/Toast';

import 'dayjs/locale/id';
import './ModalDocs.scss';


const pdfFonts = require('../../assets/vfs_fonts');

pdfMake.vfs = pdfFonts.pdfMake.vfs;
pdfMake.fonts = {
  Archivo: {
    normal: 'Archivo-Regular.ttf',
    bold: 'Archivo-SemiBold.ttf',
    italics: 'Archivo-Italic.ttf',
    bolditalics: 'Archivo-SemiBoldItalic.ttf',
  },
};

const Modal30 = ({ modal3, toggle3, sppdDetail }) => {
  const api = useAxios();

  const [detailSppd, setDetailSppd] = useState();
  const [tujuan, setTujuan] = useState([]);
  const [submitting, setSubmitting] = useState(false);

  const handleSubmitAction = async () => {
    if (!sppdDetail?.id) {
      alert('error', 'SPPD tidak valid');
      return;
    }
    if (!tujuan || tujuan.length === 0) {
      alert('error', 'Pilih minimal satu tujuan');
      return;
    }
    const payload = { sppd_id: sppdDetail.id, id_tujuan: Array.isArray(tujuan) ? tujuan : tujuan ? [tujuan] : [] };
    setSubmitting(true);
    console.log('payload 30%', payload);
    try {
      const res = await api.post('dapi/sppd/tiga-puluh', payload);
      if (res?.data?.success) {
        alert('success', res?.data?.message || 'Pengajuan berhasil');
        if (typeof toggle3 === 'function') toggle3();
      } else {
        alert('error', res?.data?.message || 'Gagal mengajukan');
      }
    } catch (err) {
      alert('error', err?.message || err);
    } finally {
      setSubmitting(false);
    }
  };

  const handleToggle = (id) => {
    // prevent toggling if the tujuan item already has a tigapuluh value (null/undefined = not submitted)
    const item = detailSppd?.find((d) => d.id === id);
    if (item?.tigapuluh != null) return;

    setTujuan((prev) => {
      if (!prev) return [id];
      if (prev.includes(id)) return prev.filter((v) => v !== id);
      return [...prev, id];
    });
  };

  useEffect(() => {
    if (sppdDetail?.id) {
      api.get(`dapi/sppd/tujuan/${sppdDetail.id}`).then((res) => {
        setDetailSppd(res.data.data || []);
        console.log('detail tujuan sppd modal 30%', res.data.data || []);
      });
    } else {
      setDetailSppd([]);
    }
  }, [sppdDetail]);
  //  console.log('sppdDetail modal 30%', sppdDetail);

  return (
    <Modal isOpen={modal3} toggle={toggle3} size="xl" className="modal1" centered>
      <ModalBody className="p-4 ">
        <h4>Pengajuan Biaya Hotel 30% <small>(<i className='text-danger'>Dalam Pengembangan / Belum bisa digunakan</i>)</small></h4>
        <hr />
        <p>Pelaku Perjalanan Dinas yang tidak menginap di Hotel/Penginapan atau tidak memiliki bill hotel bisa mengajukan 30% dari rate hotel yang disedikan oleh perusahaan. Silakan pilih tujuan Perjalanan Dinas di bawah ini untuk mengajukan</p>
        <FormGroup>
          <Label for="tujuan">Tujuan SPPD</Label>
          <div className="tujuan-box">
            <div className="tujuan-checkbox-list">
              {detailSppd?.length === 0 && <div>Tidak ada tujuan</div>}
              {detailSppd?.map((item) => (
                <div key={item.id} className="form-check mb-2">
                  <input
                    type="checkbox"
                    id={`tujuan-${item.id}`}
                    className="form-check-input"
                    checked={Array.isArray(tujuan) ? tujuan.includes(item.id) : false}
                    onChange={() => handleToggle(item.id)}
                    disabled={Boolean(item?.tigapuluh != null)}
                  />
                  <label className="form-check-label" htmlFor={`tujuan-${item.id}`}>
                    {item.detail_tujuan}
                    {item?.tigapuluh != null ? <small className="text-muted ml-2">(sudah diajukan)</small> : null}
                  </label>
                </div>
              ))}
            </div>
          </div>
        </FormGroup>
        <div className="d-flex justify-content-end mt-3">
          <button
            type="button"
            className="btn btn-primary"
            onClick={handleSubmitAction}
            disabled={submitting}
          >
            {submitting ? 'Mengirim...' : 'Ajukan 30%'}
          </button>
        </div>
      </ModalBody>
    </Modal>
  );
};

Modal30.propTypes = {
  modal3: PropTypes.bool,
  toggle3: PropTypes.func,
  sppdDetail: PropTypes.object,
};

export default Modal30;
